import {Component, h, Host, Prop, Watch} from '@stencil/core';
import {isNotEmpty} from '../../utils/utils';

@Component({
  tag: 'soul-empty-state',
  styleUrl: 'soul-empty-state.scss',
  shadow: true,
})
export class SoulEmptyState {
  @Prop({reflect: true}) heading: string;
  @Prop({reflect: true}) text: string;
  @Prop({reflect: true}) helpText: string;
  @Prop({reflect: true}) size: 's' | 'm';

  private supportedSizes = ['s', 'm'];
  private actionSlot: HTMLSlotElement;
  private additionalActionSlot: HTMLSlotElement;

  public hideEmptySlot(slot: HTMLSlotElement): void {
    slot.assignedNodes().length === 0 ?
      slot.parentElement?.classList.add('h-hidden') :
      slot.parentElement?.classList.remove('h-hidden');
  }

  @Watch('size')
  public sizeChange(newValue: string): void {
    this.validateSize(newValue);
  }

  private validateSize(newValue: string): void {
    if (!this.supportedSizes.includes(newValue)) {
      console.debug(`Size ${newValue} not supported. Supported sizes: ${this.supportedSizes.join(', ')}`);
    }
  }

  render() {
    return (
      <Host>
        <div class="soul-empty-state">
          <div class="soul-empty-state__illustration">
            <slot name="illustration"></slot>
          </div>
          <strong class="soul-empty-state__heading">{this.heading}</strong>
          {isNotEmpty(this.text) ? <p class="soul-empty-state__text">{this.text}</p> : ''}
          <div class="soul-empty-state__action h-hidden">
            <slot name="action" ref={el => this.actionSlot = el as HTMLSlotElement} onSlotchange={() => this.hideEmptySlot(this.actionSlot)}></slot>
          </div>
          <div class="soul-empty-state__additional-action h-hidden">
            <slot name="additional-action" ref={el => this.additionalActionSlot = el as HTMLSlotElement} onSlotchange={() => this.hideEmptySlot(this.additionalActionSlot)}></slot>
          </div>
          {isNotEmpty(this.helpText) ? <span class="soul-empty-state__help-text">{this.helpText}</span> : ''}
        </div>
      </Host>
    );
  }

}
